</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import { RadioGroup, Radio } from 'vant'
import 'vant/lib/index.css'
Vue.use(Radio)
Vue.use(RadioGroup)
export default {
  name: 'test',
  mounted: function () {
    this.queryones()
    this.getResData()
  },
  data () {
    return {
      mydata: [],
      checked: [],
      title: '',
      radio: '1',
      len: 0,
      sum: 0,
      active: 0,
      showTest: true,
      percentage: 10,
      showPros: false,
      showRes: false,
      res: '',
      resData: '',
      pay: false,
      dialogTableVisible: true
    }
  },
  methods: {
    // 判断是否登录
    queryones () {
      // 获取本地题目数据
      axios
        .get('test.json')
        .then(result => {
          this.mydata = result.data.data.questions
          this.len = this.mydata.length
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    next () {
      // 下一题
      if (this.checked[this.active] === undefined || '' || null) {
        this.$message.error('请选择答案')
        return
      } else {
        if (this.active++ > this.len - 1) {
          this.active = this.len - 1
        }
      }
    },
    // 上一题
    pre () {
      if (this.active-- < 0) {
        this.active = 0
      }
    },
    source (i) {
      var count = 0
      var all = 0
      for (var j = 0; j < this.len; j++) {
        count = this.checked[j]
        if (count === undefined) {
          count = 0
        }
        all = Number(all) + Number(count)
      }
      // 赋值
      this.sum = all
    },
    // 找到最多选项 确认性格
    findMost (arr) {
      this.showTest = false
      this.showPros = true
      window.setInterval(() => {
        this.percentage++
        if (this.percentage > 100) {
          this.percentage = 100
          this.showRes = true
          this.showPros = false
        }
      }, 50)
      if (!this.checked.length) return
      if (!this.checked.length === 1) return 1
      const res = {}
      let maxName
      let maxNum = 0
      // 遍历数组
      this.checked.forEach(item => {
        res[item] ? (res[item] += 1) : (res[item] = 1)
        if (res[item] > maxNum) {
          maxName = item
          maxNum = res[item]
        }
      })
      this.res = this.resData[maxName]
    },
    // 获取结果
    getResData () {
      axios
        .get('resoult.json')
        .then(result => {
          this.resData = result.data.content
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    goText () {
      this.pay = true
      this.dialogTableVisible = true
    }
  }
}
</script>
